import React from "react";
import axios from "axios";
import { useHistory } from "react-router-dom";
import "./reboot.css";
import Navbar from "../ARCASideNav/Navbar";
import { Button } from "@material-ui/core";
import reboot from "../../assets/icons_v2/reboot.svg"


const ErrorMessage = () => {
  let history = useHistory();

  async function getData() {
    const response = await axios.get("/restart");
    console.log(response);
    if (response.data.result === "Rebooted") {
      sessionStorage.clear();
      history.push("/");
      window.location.reload();
    }
  }

  return (
    <>
      <Navbar />
      <div className="container">
      <img className="reboot-Icon" src={reboot}></img>
        <h2 className="notfound">Reboot</h2>
        <h3 className="notfound-reboot">Information about reboot</h3>
        <hr style={{marginLeft:"15px", marginRight: "15px", marginTop:"-5px"}}/>

        <p className="desc">Reboots the operating system of your device</p>
        <Button
          variant="contained"
          type="submit"
          className="sbmt"
          color="primary"
          onClick={getData}
          style={{marginLeft:"15px"}}
        >
          <span style={{ color: "white" }}>
            Perform Reboot
          </span>
        </Button>
      </div>
    </>
  );
};

export default ErrorMessage;
