import { useEffect, useState } from "react";
import axios from "axios";
import {
  Switch,
  Grid,
  Typography,
  Checkbox,
  Button,
  CircularProgress,
  Snackbar,
  TextField,
} from "@material-ui/core";
import load_balance from "../../assets/icons_v2/load_balance.svg";
import "./LoadBalancer.css";
import Navbar from "../ARCASideNav/Navbar";
import LogoutPopup from "../../LogoutPopup";

const LoadBalancer = () => {
  const [toggleLoad, setToggleLoad] = useState(true);
  const [toggleBalancer, setToggleBalancer] = useState(false);
  const [loadData, setLoadData] = useState();
  const [checkboxVal, setCheckboxVal] = useState(true);
  const [loadBalenceData, setLoadBalenceData] = useState({});
  const [open, setOpen] = useState(false);
  const [open2, setOpen2] = useState(false);
  const [errors, setErrors] = useState({});

  const [open3, setOpen3] = useState(false);
  const handleClose3 = () => {
    setOpen3(false);
  };

  const handleClose = () => {
    setOpen(false);
  };

  const handleClose2 = () => {
    setOpen2(false);
  };

  const toggleBalancerChange = (event) => {
    setToggleBalancer(event.target.checked);
    if (event.target.checked === true) {
      const res = axios.get("/lbon");
    } else if (event.target.checked === false) {
      const res = axios.get("/lboff");
    }
  };
  const toggler = (event) => {
    setToggleLoad(event.target.checked);
    if (event.target.checked === true) {
      const res = axios
        .get("/loadbalancingon")
        .catch((err) => console.log(err));
    } else if (event.target.checked === false) {
      const resp = axios
        .get("/loadbalancingoff")
        .catch((err) => console.log(err));
    }
  };

  async function getData() {
    try {
      const res = await axios.get("/getloadbalance");
    } catch (error) {
      console.error(error);
    }
  }
  useEffect(() => {
    getData();
    const interval = setInterval(() => {
      getData();
    }, 10000);
    return () => clearInterval(interval);
  }, []);

  useEffect(() => {
    async function getStatus() {
      try {
        const res = await axios.get("/loadbalancingstatus");
        setToggleLoad(res.data.result);
        const loaddata = await axios.get("/getloadbalance");
        if (loaddata.data) {
          setLoadData(loaddata.data);
          const initialKeys = ["ispName", "interfaceName", "checked", "weight"];
          const loadBalencerDataArray = [];
          loaddata.data.forEach((element) => {
            const itemData = {};
            itemData[initialKeys[0]] = element[0];
            itemData[initialKeys[1]] = element[1];
            itemData[initialKeys[2]] = element[2];
            itemData[initialKeys[3]] = element[3];
            loadBalencerDataArray.push(itemData);
          });
          setLoadBalenceData({ balencerData: loadBalencerDataArray });
        } else {
          setLoadData([]);
        }
        const toggle = await axios.get("/lbstatus");
        setToggleBalancer(toggle.data.result);
        setCheckboxVal();
      } catch (error) {
        console.log(error);
      }
    }
    getStatus();
  }, []);
  const handleChangeChecked = (e, item) => {
    let updateList = loadBalenceData.balencerData.map((obj) => {
      if (obj.interfaceName === item.interfaceName) {
        return Object.assign({}, obj, {
          checked: e.target.checked,
        });
      }
      return obj;
    });
    setLoadBalenceData({ balencerData: updateList });
  };

  useEffect(() => {
    const res = axios.post("/setlbinterface", loadBalenceData.balencerData);
  }, [loadBalenceData.balencerData]);

  const handleChangeWeight = (e, item) => {
    let updatedList1 = loadBalenceData.balencerData.map((obj) => {
      if (obj.interfaceName === item.interfaceName) {
        return Object.assign({}, obj, {
          weight: e.target.value,
        });
      }
      return obj;
    });
    if (e.target.value > 100) {
      setOpen3(true);
      return false;
    }
    setLoadBalenceData({ balencerData: updatedList1 });
  };
  const handleSave = () => {
    async function saveCheck() {
      const res = await axios.post(
        "/setloadbalance",
        loadBalenceData.balencerData
      );
      if (res.data.result === "Success") {
        setOpen(true);
      } else if (res.data.result === "failed") {
        setOpen2(true);
      }
    }
    saveCheck();
  };

  return (
    <>
      <Navbar />
      <LogoutPopup />
      <div className="load-container">
        <Grid container spacing={2}>
          <Grid item xs={8}>
            <div style={{ marginLeft: 10 }}>
              <img src={load_balance} className="load-icon" />
              <h1 className="lb-heading">Load Balancer</h1>
              <h2 className="lbsub-heading">Information about load balancer</h2>
            </div>
          </Grid>
          <Grid xs={3}>
            <div style={{ marginTop: "40px", marginLeft: "62px" }}>
              <Typography component="div">
                <Grid item style={{ marginLeft: -20, marginBottom: -33 }}>
                  Off
                </Grid>
                <Grid
                  component="label"
                  container
                  alignItems="center"
                  spacing={1}
                >
                  <Grid item>
                    <Switch
                      checked={toggleLoad}
                      onChange={toggler}
                      color="primary"
                      name="speedboost"
                      inputProps={{ "aria-label": "primary checkbox" }}
                    />
                  </Grid>
                </Grid>
                <Grid item style={{ marginLeft: 60, marginTop: -33 }}>
                  On
                </Grid>
              </Typography>
            </div>
          </Grid>
        </Grid>
        <hr
          style={{
            marginLeft: 15,
            marginRight: 17,
            color: "#E0E5E5",
            marginTop: -7,
          }}
        />
        {loadBalenceData.balencerData ? (
          <>
            <div style={{ margin: "15px", marginTop: "-10px" }}>
              <Typography component="div">
                <Grid
                  component="label"
                  container
                  alignItems="center"
                  spacing={2}
                >
                  <Grid item>
                    <Typography
                      className="toggle-head"
                      style={!toggleLoad ? { color: "#dddddd" } : {}}
                      style={
                        !toggleBalancer
                          ? { color: "#ef8029" }
                          : { color: "rgb(193 188 188" }
                      }
                    >
                      Static Load Balancer
                    </Typography>
                  </Grid>
                  <Grid item>
                    <Switch
                      checked={toggleBalancer}
                      onChange={toggleBalancerChange}
                      color="default"
                      name="speedboost"
                      inputProps={{ "aria-label": "Switch A" }}
                      disabled={!toggleLoad}
                    />
                  </Grid>
                  <Grid item>
                    <Typography
                      className="toggle-head"
                      style={!toggleLoad ? { color: "#dddddd" } : {}}
                      style={
                        toggleBalancer
                          ? { color: "#ef8029" }
                          : { color: "rgb(193 188 188" }
                      }
                    >
                      Dynamic Load Balancer
                    </Typography>
                  </Grid>
                </Grid>
              </Typography>
            </div>
            <div style={{ margin: "15px", marginTop: "-10px" }}>
              {!toggleBalancer ? (
                <table>
                  <thead>
                    <tr style={!toggleLoad ? { color: "#f3efef" } : {}}>
                      <th style={{ textAlign: "center" }}>Interface</th>
                      <th style={{ textAlign: "start" }}>ISP Name</th>
                      <th style={{ textAlign: "start" }}>Weight(%)</th>
                    </tr>
                  </thead>
                  <tbody>
                    {loadBalenceData.balencerData.map((item, index) => {
                      return (
                        <tr
                          key={item.interfaceName}
                          style={!toggleLoad ? { color: "#dddddd" } : {}}
                        >
                          <td
                            className="interface-Name"
                            style={
                              !item.checked
                                ? { textAlign: "start", color: "#dddddd" }
                                : {}
                            }
                          >
                            <Checkbox
                              color="primary"
                              checked={item.checked}
                              onChange={(e) => {
                                handleChangeChecked(e, item);
                              }}
                              inputProps={{ "aria-label": "secondry checkbox" }}
                              disabled={!toggleLoad}
                              //style={!item.checked ? { textAlign: 'start', color: '#dddddd' } : {}}
                            />
                            {item.interfaceName}
                          </td>
                          <td
                            className="isp-Name"
                            style={
                              !item.checked
                                ? { textAlign: "start", color: "#dddddd" }
                                : {}
                            }
                          >
                            {item.ispName}
                          </td>
                          <td style={{ textAlign: "start", padding: "8px" }}>
                            <TextField
                              type="text"
                              id="load-input"
                              name="weight"
                              color="primary"
                              variant="outlined"
                              onChange={(e) => {
                                handleChangeWeight(e, item);
                              }}
                              style={
                                !item.checked || !toggleLoad
                                  ? {
                                      width: "43px",
                                      height: "25px",
                                      color: "#dddddd",
                                      borderColor: "#dddddd",
                                      textAlign: "center",
                                    }
                                  : {
                                      width: "43px",
                                      height: "25px",
                                      textAlign: "center",
                                    }
                              }
                              value={item.weight}
                              disabled={!toggleLoad || !item.checked}
                              inputProps={{ maxLength: 3 }}
                              onInput={(e) => {
                                e.target.value = e.target.value.replace(
                                  /[^0-9]/g,
                                  ""
                                );
                              }}
                              error={errors?.weight}
                              helperText={errors?.weight}
                            />
                            <Snackbar
                              open={open3}
                              autoHideDuration={3000}
                              onClose={handleClose3}
                              action={
                                <>
                                  <div
                                    style={{
                                      display: "flex",
                                      placeContent: "center",
                                      marginRight: 10,
                                    }}
                                  >
                                    Invalid Weight
                                  </div>
                                </>
                              }
                            ></Snackbar>
                          </td>
                        </tr>
                      );
                    })}
                  </tbody>
                </table>
              ) : (
                <table>
                  <thead>
                    <tr style={!toggleLoad ? { color: "#f3efef" } : {}}>
                      <th style={{ textAlign: "center" }}>Interface</th>
                      <th style={{ textAlign: "start" }}>ISP Name</th>
                      <th style={{ textAlign: "start" }}>Weight(%)</th>
                    </tr>
                  </thead>
                  <tbody>
                    {loadBalenceData.balencerData?.map((item, index) => {
                      return (
                        <tr
                          key={item.index}
                          style={!toggleLoad ? { color: "#dddddd" } : {}}
                        >
                          <td
                            className="interface-Name"
                            style={
                              !item.checked
                                ? { textAlign: "start", color: "#dddddd" }
                                : {}
                            }
                          >
                            <Checkbox
                              color="primary"
                              checked={item.checked}
                              onChange={(e) => {
                                handleChangeChecked(e, item);
                              }}
                              inputProps={{ "aria-label": "secondry checkbox" }}
                              disabled={!toggleLoad}
                              //style={!item.checked ? { textAlign: 'start', color: '#dddddd' } : {}}
                            />
                            {item.interfaceName}
                          </td>
                          <td
                            className="isp-Name"
                            style={
                              !item.checked
                                ? { textAlign: "start", color: "#dddddd" }
                                : {}
                            }
                          >
                            {item.ispName}
                          </td>
                          {/* <td style={!item.checked ? { textAlign: 'start', color: '#dddddd' } : {padding: 13}}>{item.weight} */}
                          <td
                            style={
                              !item.checked
                                ? { textAlign: "start", color: "#dddddd", padding: "8px" }
                                : {padding: "8px"}
                            }
                          >
                            {item.weight}
                          </td>
                        </tr>
                      );
                    })}
                  </tbody>
                </table>
              )}
            </div>
            <Button
              type="submit"
              variant="contained"
              color="primary"
              size="small"
              style={{ marginLeft: 15, marginBottom: 25, width: 85 }}
              disabled={!toggleLoad}
              onClick={handleSave}
            >
              <span
                style={{
                  color: "white",
                }}
              >
                Save
              </span>
            </Button>
            {open ? (
              <Snackbar
                open={open}
                autoHideDuration={3000}
                onClose={handleClose}
                action={
                  <>
                    <div style={{ display: "flex", placeContent: "center" }}>
                      Saved Successfully
                    </div>
                  </>
                }
              ></Snackbar>
            ) : (
              ""
            )}
            {open2 ? (
              <Snackbar
                open={open2}
                autoHideDuration={3000}
                onClose={handleClose2}
                action={
                  <>
                    <div style={{ display: "flex", placeContent: "center" }}>
                      Total Sum should be 100!
                    </div>
                  </>
                }
              ></Snackbar>
            ) : (
              ""
            )}
          </>
        ) : (
          <div className="load-loader">
            <CircularProgress />
          </div>
        )}
      </div>
    </>
  );
};
export default LoadBalancer;
